.MovieDetails {
    width: 100%;
    margin: auto;
    text-align: center;
    background-color: #f7f0f0;
}

.ImagesContainer {
    position: relative;
}

.BackgroundImage {
    width: 100%;
    height: 400px;
    object-fit: cover;
    filter: blur(5px);
    z-index: -10;
}

.PosterImage {
    height: 400px;;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
}

.DetailsContainer {
    width: 100%;
    border-radius: 50px;
    margin: auto;
    margin-top: -100px;
    padding-top: 100px;
    padding-bottom: 1px;
    background-color: #ff5000;
    position: relative;
    z-index: 1;
}

.Details {
    width: 65%;
    margin: auto;
    margin-top: -50px;
    margin-bottom: 20px;
    padding-top: 50px;
    padding-bottom: 20px;
    border-radius: 10px;
    background-color: white;
}

.Details h4 {
    margin-top: 30px;
    margin-bottom: 0px;
}

.Details p {
    margin: 0 auto;
}



.CastListContainer {
    position: relative;
    border: 1px solid black;
}

.CastList {
    border: 1px solid red;
}